* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

body {
  background: #ececec;
  font-family: 'OpenSans-Regular';
}

img {
  width: 100%;
}

.wrap {
  max-width: 1170px;
  margin: auto;
  padding: 0 15px;
}

.header .slider .item {
  position: relative;
}

.header .slider .item .wrap {
  position: absolute;
  display: flex;
  align-items: center;
  text-align: center;
  top: 110px;
  left: 0;
  right: 0;
}

.header .slider .item .wrap .caption {
  margin-left: 30px;
  font-family: 'HelveticaNeueCyr-Black';
}

.header .slider .item .wrap .caption p {
  font-size: 15px;
  background: green;
  color: #fff;
  text-transform: uppercase;
  padding: 5px 10px;
  width: 437px;
}

.header .slider .item .wrap .caption h2 {
  font-size: 44px;
  text-transform: uppercase;
  background: rgba(84, 94, 104, 0.6);
  padding: 21px 15px;
  color: #fff;
  margin-top: -10px;
}

.header .slider .item .wrap .caption .button {
  display: flex;
  font-family: 'OpenSans-Light';
}

.header .slider .owl-dots {
  display: flex;
  margin: auto;
  position: absolute;
  padding: 11px;
  left: 0;
  right: 0;
  width: 120px;
  justify-content: space-around;
  bottom: 20px;
}

.header .slider .owl-dots .owl-dot {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  color: black;
  background: #fff;
}

.header .slider .owl-dots .owl-dot.active {
  background: purple;
  border: 3px solid #fff;
}

.header .info .wrap {
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  z-index: 10;
}

.header .info .wrap .languages {
  float: left;
  display: flex;
  text-align: center;
  font-family: 'OpenSans-Regular';
  font-size: 12px;
}

.header .info .wrap .languages a {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: purple;
  color: #fff;
  margin: 5px;
  padding-top: 5px;
  text-decoration: none;
}

.header .info .wrap .languages a:hover {
  background: #fff;
  color: purple;
  transition: .5s;
}

.header .info .wrap .languages a:nth-child(2) {
  background: #fff;
  color: purple;
}

.header .info .wrap .languages a:nth-child(2):hover {
  background: purple;
  color: #fff;
}

.header .info .wrap .contacts {
  float: right;
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: right;
  font-family: 'HelveticaNeueCyr-Light';
}

.header .info .wrap .contacts .phone {
  background: purple;
  color: #fff;
  padding: 7px 10px 0;
}

.header .info .wrap .contacts .mail {
  background: #fff;
  color: purple;
  padding: 7px 10px 0;
  letter-spacing: 0.5px;
}

.header .info .wrap .contacts span {
  font-family: 'HelveticaNeueCyr-Bold';
}

.header .info .wrap .contacts i {
  padding: 0 5px;
}

.nav {
  background: #fff;
  border-bottom: 1px solid #ededed;
}

.nav .wrap #burger {
  position: absolute;
  margin-top: 10px;
  margin-left: 15px;
}

.nav .wrap .collapse {
  display: flex;
}

.nav .wrap .collapse ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 756px;
  margin-left: 70px;
  margin-top: 3px;
  margin-bottom: 3px;
  font-family: 'OpenSans-Light';
  font-size: 14px;
  text-transform: uppercase;
  position: relative;
}

.nav .wrap .collapse ul li {
  padding: 10px 14.2px;
}

.nav .wrap .collapse ul li:hover {
  background: #eee;
}

.nav .wrap .collapse ul li a {
  text-decoration: none;
  color: purple;
}

.nav .wrap .collapse ul li i {
  background: #fff;
  border: 3px solid #fff;
  border-radius: 50%;
  font-size: 10px;
}

.nav .wrap .collapse ul li:hover .fa-chevron-down {
  background: purple;
  border: 3px solid purple;
  color: #fff;
}

.nav .wrap .collapse ul .menu1 .dropbtn {
  font-size: 14px;
  border: none;
  color: purple;
  background: none;
  text-transform: uppercase;
}

.nav .wrap .collapse ul .menu1 .menu_content1 {
  display: none;
  position: absolute;
  left: 23px;
  top: 35px;
  background: purple;
  color: #fff;
  z-index: 1;
  line-height: 25px;
}

.nav .wrap .collapse ul .menu1 .menu_content1 li {
  display: block;
  padding: 6px 10px;
}

.nav .wrap .collapse ul .menu1 .menu_content1 li a {
  text-decoration: none;
  color: white;
}

.nav .wrap .collapse ul .menu1 .menu_content1 li:hover {
  background: green;
}

.nav .wrap .collapse ul .menu1:hover .menu_content1 {
  display: block;
}

.nav .wrap .collapse ul .menu2 .dropbtn {
  font-size: 14px;
  border: none;
  color: purple;
  background: none;
  text-transform: uppercase;
}

.nav .wrap .collapse ul .menu2 .menu_content2 {
  display: none;
  position: absolute;
  left: 116px;
  top: 35px;
  background: purple;
  color: #fff;
  z-index: 1;
  line-height: 25px;
  min-width: 149px;
}

.nav .wrap .collapse ul .menu2 .menu_content2 li {
  display: block;
  padding: 6px 10px;
}

.nav .wrap .collapse ul .menu2 .menu_content2 li a {
  text-decoration: none;
  color: white;
}

.nav .wrap .collapse ul .menu2 .menu_content2 li:hover {
  background: green;
}

.nav .wrap .collapse ul .menu2:hover .menu_content2 {
  display: block;
}

.nav .wrap .collapse ul .menu3 .dropbtn {
  font-size: 14px;
  border: none;
  color: purple;
  background: none;
  text-transform: uppercase;
}

.nav .wrap .collapse ul .menu3 .menu_content3 {
  display: none;
  position: absolute;
  left: 267px;
  top: 35px;
  background: purple;
  color: #fff;
  z-index: 1;
  line-height: 25px;
}

.nav .wrap .collapse ul .menu3 .menu_content3 li {
  display: block;
  padding: 6px 10px;
}

.nav .wrap .collapse ul .menu3 .menu_content3 li a {
  text-decoration: none;
  color: white;
}

.nav .wrap .collapse ul .menu3 .menu_content3 li:hover {
  background: green;
}

.nav .wrap .collapse ul .menu3:hover .menu_content3 {
  display: block;
}

.nav .wrap .collapse .form .search {
  display: none;
  width: 90%;
  position: absolute;
  top: 1px;
  right: 15px;
  padding: 8px;
  border: 1px solid #ccc;
  margin-top: 3px;
}

.nav .wrap .collapse .form .search_button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 9.5px 15px;
  margin-top: 3px;
  border: none;
  color: #fff;
  background: purple;
}

.nav .wrap .collapse .form .search_button:active {
  background: red;
}

.nav .wrap .collapse .order_button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 9.5px 20px;
  margin-top: 3px;
  border: none;
  color: #fff;
  background: purple;
  text-transform: uppercase;
  text-decoration: none;
}

.section_top {
  background: url('../img/section_top_background.png');
  background-color: #fff;
  padding: 60px 0;
}

.section_top .wrap h2 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 32px;
  color: purple;
  margin-top: 0;
  margin-bottom: 20px;
}

.section_top .wrap p {
  font-family: 'OpenSans-Light';
  font-size: 15px;
  color: #828282;
}

.section_top .wrap a {
  color: purple;
  text-transform: uppercase;
  text-decoration: underline;
}

.section_top .wrap .item {
  background: url('../img/section_top_form_background.png');
  min-height: 360px;
  padding: 20px 27px;
  color: #fff;
}

.section_top .wrap .item h2 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 26px;
  margin-top: 15px;
  margin-bottom: 15px;
  color: inherit;
}

.section_top .wrap .item p {
  font-family: 'OpenSans-Light';
  font-size: 14px;
  text-transform: uppercase;
  margin-top: 15px;
  margin-bottom: 5px;
  color: inherit;
}

.section_top .wrap .item input {
  color: #aaa;
  min-height: 30px;
  width: 46%;
}

.section_top .wrap .item .city {
  width: 99%;
}

.section_top .wrap .item .arrive {
  margin-left: 30px;
}

.section_top .wrap .item .button {
  display: block;
  padding-left: 15px;
  margin-top: 18px;
  color: inherit;
  text-decoration: none;
}

.section_info {
  padding: 35px 0;
  background: #f9f3de;
}

.section_info .wrap .item {
  position: relative;
  height: 155px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.section_info .wrap .item .image {
  margin-bottom: 30px;
}

.section_info .wrap .item h2 {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  margin: auto;
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 26px;
  color: purple;
}

.section_info .wrap .item .item_hide {
  background: purple;
  display: none;
}

.section_info .wrap .item:hover .item_hide {
  position: absolute;
  display: block;
  width: 100%;
  height: 145%;
  top: -35px;
  left: 0;
  transition: 0.4s;
  text-align: center;
  cursor: pointer;
}

.section_info .wrap .item:hover .item_hide h2 {
  font-family: 'HelveticaNeueCyr-Bold';
  font-size: 26px;
  color: #fff;
  position: absolute;
  top: 30px;
}

.section_info .wrap .item:hover .item_hide p {
  font-family: 'OpenSans-Light';
  font-size: 15px;
  color: #fff;
  position: absolute;
  top: 70px;
}

.section_offers {
  background: #fff;
}

.section_offers .wrap .advertising {
  margin-top: 40px;
}

.section_offers .wrap .advertising .col-md-9 h2 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 32px;
  color: purple;
}

.section_offers .wrap .advertising .col-md-9 .item {
  border: 1px solid #ccc;
