}

.section_offers .wrap .advertising .col-md-9 .item .desc h3 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 20px;
  color: #828282;
}

.section_offers .wrap .advertising .col-md-9 .item .desc p {
  font-family: 'OpenSans-Light';
  font-size: 15px;
  color: green;
}

.section_offers .wrap .advertising .col-md-9 .item .desc .date {
  font-size: 13px;
