}

.section_offers .wrap .advertising .col-md-9 .item .desc .sale {
  position: absolute;
  top: 45px;
  left: 7px;
  background: green;
  color: #fff;
  padding: 5px;
  text-transform: uppercase;
  font-family: 'HelveticaNeueCyr-Light';
