}

.section_offers .wrap .advertising .col-md-9 .item .item_hide {
  display: none;
}

.section_offers .wrap .advertising .col-md-9 .item:hover .item_hide {
  height: 344px;
  width: 255px;
  background: purple;
  display: block;
  position: absolute;
  top: 30px;
  right: 15px;
  cursor: pointer;
}

.section_offers .wrap .advertising .col-md-9 .item:hover .item_hide img {
  width: 118px;
  margin: 25px 60px;
}

.section_offers .wrap .advertising .col-md-9 .item:hover .item_hide h3 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 20px;
  color: #fff;
  margin-left: 10px;
}

.section_offers .wrap .advertising .col-md-9 .item:hover .item_hide p {
  font-family: 'OpenSans-Light';
  font-size: 15px;
  color: #8a8a8a;
  margin-left: 10px;
}

.section_offers .wrap .advertising .col-md-9 .item:hover .item_hide a {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 20px;
  color: #fff;
  display: inline-block;
  margin: 20px 10px;
  text-decoration: none;
}

.section_offers .wrap .advertising .col-md-9 .item:hover .item_hide a i {
  margin-left: 5px;
  font-size: 15px;
}

.section_offers .wrap .advertising .button {
  float: right;
  min-width: 165px;
  padding: 10px 12px;
  margin-bottom: 10px;
  margin-top: 65px;
}

.section_offers .wrap .aside .col-md-3 h2 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 26px;
  color: purple;
  margin-bottom: 40px;
}

.section_offers .wrap .aside .col-md-3 .item span {
  font-family: 'OpenSans-Light';
