  color: green;
}

.section_offers .wrap .aside .col-md-3 .item h2 {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 20px;
  color: #8a8a8a;
  margin-top: 18px;
  margin-bottom: 18px;
}

.section_offers .wrap .aside .col-md-3 .item p {
  font-family: 'OpenSans-Light';
  font-size: 15px;
  color: #8a8a8a;
}

.section_offers .wrap .aside .col-md-3 a {
  font-family: 'OpenSans-Light';
  font-size: 12px;
  text-transform: uppercase;
  color: purple;
  text-decoration: underline;
}

.section_offers .wrap .aside .col-md-3 h2 {
  margin-top: 40px;
}

.section_offers .wrap .aside .col-md-3 .slider {
  margin-bottom: 50px;
}

.section_offers .wrap .aside .col-md-3 .slider .owl-dots {
  position: absolute;
}

.section_offers .wrap .aside .col-md-3 .slider .owl-dots .owl-dot span {
  border-radius: 50%;
  background: #fff;
  border: 1px solid #e6e6e6;
}

.section_offers .wrap .aside .col-md-3 .slider .owl-dots .owl-dot.active span {
  border: 2px solid #e6e6e6;
  background: purple;
}

.section_review {
